import React, { Component } from 'react';
import {Card, Table, Spinner} from 'reactstrap';
import moment from 'moment';
import 'moment/locale/id'
import _ from 'lodash'

export default class QueueLists extends Component {
  state = {
    queues : {
      count : 1,
      rows : []
    },
    loading : false,
  }

  componentDidMount = () => {
    const {socket} = this.props;
    moment.locale('id');
    this.getQueues(true);
    socket.on('UPDATE_LIST', () => this.getQueues(false));
    socket.on('NEW_QUEUE', () => this.getQueues(false));
    socket.on('QUEUE_CALLED', () => this.getQueues(false));
    socket.on('QUEUE_ARRIVED', () => this.getQueues(false));
  };
  
  getQueues = (loading = false) => {
    const {models : { Queue }} = this.props;
    if (loading) this.setState({ loading : true });
    Queue.collection({ limit :  25, offset : 0, where : { date :  moment(new Date()).format('YYYY-MM-DD'), called : 0}, orderBy : [['time', 'asc'], ['id', 'asc']] ,include : [{
      model : 'Purpose',
      attributes : ['id', 'name']
    }] }).then(queues => {
      queues.rows = _.sortBy(queues.rows, e => e.time)
      this.setState({ queues, loading : false })
    })
  }

  render() {
    return (
      <>
        <div className="bg-half" />
        <div className="wrapper p-3">
          <Card style={{ height: '100%' }} className="border-0 shadow overflow-scroll">
            <div className="p-3">
              <h3 className="m-0 text-center">Pengantri</h3>
              <h5 className="m-0 text-center">Hari {moment().format('dddd, DD MMMM YYYY')}</h5>
              <p className="m-0 text-center">Jumlah pengantri : {this.state.queues.count}</p>
            </div>
            <Table bordered striped>
              <thead>
                <tr>
                  <th>No</th>
                  <th>Nama</th>
                  <th>NIK</th>
                  <th>Nomor Pengantri</th>
                  <th>Jam</th>
                  <th>Tujuan</th>
                </tr>
              </thead>
              <tbody>
                { this.state.loading &&
                  <tr>
                    <td colSpan={document.getElementsByTagName('th').length}>
                      <h4 className="m-0 text-center"><Spinner size="sm" />&nbsp;Loading</h4>
                    </td>
                  </tr>
                }
                { !this.state.loading && this.state.queues.rows.map((row, i) => (<Tr index={i + 1} {...row} />)) }
                { !this.state.loading && this.state.queues.rows.length === 0 &&
                  <tr>
                    <td colSpan={document.getElementsByTagName('th').length}>
                      <h4 className="m-0 text-center">Tidak ada antrian</h4>
                    </td>
                  </tr>
                }
              </tbody>
            </Table>
          </Card>
        </div>
      </>
    )
  }
}

const Tr = props => (
  <tr>
    <td>{props.index}</td>
    <td>{props.name.toUpperCase()}</td>
    <td>{props.nik}</td>
    <td>{props.queue_number}</td>
    <td>{props.time}</td>
    <td>{props.purpose.name}</td>
  </tr>
)