import React, { Component } from 'react';
import LoginForm from '../components/LoginForm';
import { Redirect } from 'react-router-dom'

export default class Login extends Component {
  state = {
    login: false,
    loading: false,
    error : false
  }

  componentDidMount() {
    const { login } = this.props;
    this.setState({ login });
  }

  componentWillReceiveProps = props => {
    const { login } = props;
    this.setState({ login });
  }

  login = (username, password) => {
    this.setState({ loading : true, error : false });
    const { provider, check } = this.props;
    provider.set({ username, password }).then(() => {
      this.setState({ login: true, loading : false }, () => check())
    }).catch(e => {
      this.setState({ error : true, loading : false });
      console.log(e)
    })
  };

  render() {
    document.title = "Login ke Dashboard"
    if (this.props.login) return <Redirect to="/dashboard/main" />
    return (
      <>
        <LoginForm {...this.props} error={this.state.error} loading={this.state.loading} login={this.login} />
      </>
    )
  }
}
