import React, { Component, Suspense, lazy } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import SiriusAdapter from '@edgarjeremy/sirius.adapter';
import socket from 'socket.io-client';
import { loadReCaptcha } from 'react-recaptcha-v3'
import Login from './pages/Login';
import QueueLists from './pages/QueueLists';
import './styles/main.scss';
import logo from './logo.png'
const Dashboard = lazy(() => import('./pages/Dashboard'));
const MainPage = lazy(() => import('./pages/MainPage'));
const Check = lazy(() => import('./pages/Check'));
const Register = lazy(() => import('./pages/Register'));

const config = process.env
const Adapter = new SiriusAdapter(config.REACT_APP_IP_ADDRESS, config.REACT_APP_PORT, localStorage);
const io = socket(`${config.REACT_APP_IP_ADDRESS}:${config.REACT_APP_PORT}`)

class App extends Component {
  state = {
    ready: false,
    models: {},
    provider: {},
    error: false,
    user: { type: '...', name: 'Loading' },
    login: false,
    dark: false
  };

  componentDidMount = async () => {
    try {
      const models = await Adapter.connect()
      const provider = await Adapter.getAuthProvider()
      loadReCaptcha('6Lc259UUAAAAANmApCOJJboq3_AeAYTZiJqg5M6W');
      provider.get().then(user => {
        this.setState({ models, provider, ready: true, user, login: true })
      }).catch(() => this.setState({ login: false, ready: true, models, provider, }))
    } catch (e) {
      console.log(e)
      this.setState({ error: true, ready: true });
    }
    document.addEventListener("keydown", e => {
      if (e.ctrlKey && e.altKey && e.key === 'âˆ‚') {
        console.log(localStorage.getItem('dark') === 'true')
        if (localStorage.getItem('dark') === 'true') {
          localStorage.setItem("dark", 'false');
          this.setState({ dark: false })
        } else if (localStorage.getItem('dark') === 'false') {
          localStorage.setItem("dark", 'true');
          this.setState({ dark: true })
        } else {
          localStorage.setItem("dark", 'true');
          this.setState({ dark: true })
        }
        // if (typeof localStorage.getItem("dark") !== 'null') {
        // } else {
        //   console.log('t')
        // }
      }
    })
    const dark = localStorage.getItem('dark');
    if (dark === 'true') {
      this.setState({ dark: true })
    } else if (dark === 'false') {
      this.setState({ dark: false })
    }
    console.log(localStorage.getItem('dark'));
  }

  check = () => {
    const { provider } = this.state;
    provider.get().then(user => {
      this.setState({ ready: true, user, login: true })
    }).catch(() => this.setState({ login: false, ready: true }))
  }


  render() {
    if (this.state.error) return (<ErrorMsg />)
    return (
      // <>
      !this.state.ready ? <Loading msg="Loading" /> :
        <Suspense fallback={<Loading msg="Tunggu Sebentar" />}>
          <Router>
            <Route path="/" exact render={(p) => (<MainPage {...p} dark={this.state.dark} user={this.state.user} />)} />
            <Route path="/cek" exact render={p => (<Check {...p} models={this.state.models} />)} />
            <Route path="/register" render={p => <Register models={this.state.models} {...p} />} />
            <Route path="/dashboard" render={(p) => (<Dashboard provider={this.state.provider} check={this.check} user={this.state.user} login={this.state.login} models={this.state.models} socket={io} {...p} />)} />
            <Route path="/login" render={(p) => (<Login provider={this.state.provider} check={this.check} user={this.state.user} login={this.state.login} {...p} />)} />
            <Route path="/list" render={p => (<QueueLists models={this.state.models} socket={io} {...p} />)} />
          </Router>
        </Suspense>
      // </>
    );
  }
}

const Loading = props => (<div className="container-fluid bg-loading vh-100 flex-column d-flex justify-content-center align-items-center">
  <div className="w-25 my-3">
    <img src={logo} className="mdo-logo d-block m-auto" alt="" />
  </div>
  <h4 className="text-white my-3">{props.msg}</h4>
  <div className="spinner-border text-white" />
</div>)

const ErrorMsg = () => (
  <div className="container-fluid bg-loading vh-100 flex-column d-flex justify-content-center align-items-center">
    <div className="w-25 my-3">
      <img src={logo} className="mdo-logo d-block m-auto" alt="" />
    </div>
    <h4 className="text-white">Terjadi Kesalahan</h4>
    <p className="text-white m-0">Silakan reload (muat ulang) halaman ini</p>
  </div>
)

export default App;
