import React, { Component } from 'react';
import {Alert, Spinner} from 'reactstrap';
import {Link} from 'react-router-dom';

export default class LoginForm extends Component {
  state = {
    username: '',
    password: '',
    loading : false,
    error : false
  };

  onChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
  }

  login = ev => {
    ev.preventDefault();
    const { username, password } = this.state;
    this.props.login(username, password);
  }

  componentWillReceiveProps = props => {
    const {loading, error} = props;
    this.setState({ loading, error });
  }

  _dissmissError = () => {
    this.setState({ error : false });
  }

  render() {
    const { error } = this.state;
    return (
      <>
        <div className="bg-half animated slideInDown fast" />
        <Link className="login-btn" to="/">Home</Link>
        <div className="wrapper d-flex justify-content-center align-items-center">
          <div style={{ maxWidth: 600 }} className="container">
            <div className="card border-0 shadow">
              <div className="card-body">
                <h3 className="mb-2">Login</h3>
                <form className="form-group">
                  <input disabled={this.state.loading} type="text" onChange={this.onChange} name="username" value={this.state.username} placeholder="Username" className={`form-control ${error ? 'is-invalid' : ''} rounded-pill my-2`} />
                  <input disabled={this.state.loading} type="password" onChange={this.onChange} name="password" value={this.state.password} placeholder="Password" className={`form-control ${error ? 'is-invalid' : ''} rounded-pill my-2`} />
                  <hr />
                  { this.state.loading &&
                    <>
                        <Alert size="sm" color="success"><Spinner size="sm" color="success"/>&nbsp;Loading</Alert>
                        <hr />
                    </>
                  }
                  <Alert isOpen={error} color="danger" toggle={this._dissmissError}>Login tidak valid</Alert>
                  <button type="submit" onClick={this.login} className="btn btn-outline-success rounded-pill btn-block">Login</button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}
